.show {
  list-style: none;
  font-size: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 60vh;
  padding: 0;
}

.show a {
  color: #fff;
  flex-grow: 1;
}

.show li {
  color: #fff;
  flex-grow: 2;
  width: 100%;
  text-align: center;
}

.show li:nth-child(4n) {
  flex-grow: 1;
}
.hide {
  display: none;
}
@media (min-width: 760px) {
  nav {
    display: none;
  }
}
