.slider_container {
  display: flex;
  justify-content: space-between;
  margin: 70px 0;
  align-items: center;
}

.slider_item {
  width: 400px;
  height: 225px;
  left: 94px;
  top: 970px;
  padding: 22px 20px;
  background: #ffffff;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

.slider_item p {
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.01em;
}
@media (max-width: 1460px) {
  .slider_item {
    width: 350px;
  }
}

@media (max-width: 1320px) {
  .slider_item {
    width: 300px;
  }
}
