.footer {
  height: 137px;
  background-color: #029491;
  color: #ffffff;
  position: relative;
}
.footer img {
  margin-left: 60px;
}
.info {
  position: absolute;
  top: 25px;
  right: 45px;
  text-decoration: none;
  font-weight: 400;
  font-size: 14px;
  text-align: right;
}
.info address {
  font-style: normal;
}
.info a {
  text-decoration: none;
  color: #ffffff;
}
@media (max-width: 430px) {
  .footer img {
    margin-left: 18px;
  }
  .info {
    right: 19px;
  }
}
